import { Injectable, Inject } from '@angular/core';
import { RestUtil } from '../../utils/rest-util';
import { CoreConfig } from '../../config';



/**
 * Services for analytics reports - for account administrator only
 * @RequestHeader X-API-KEY The key to identify the application (portal)
 * @RequestHeader X-ACCESS-TOKEN The token to identify the logged-in user 
 */
@Injectable()
export class AdminReportsService {

  // URL to web api
  private baseUrl = '/admin/reports';

  /**
   * Class constructor
   */
  constructor(@Inject('config') private config: CoreConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Get count of users overtime
   * @Return: EntitiesResponse<CountDataPoint>
   */
  getUsersCountOvertime(from?: number, to?: number) {
    const params = new Array();		
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }

		return this.rest.get(`${this.baseUrl}/usage-overtime`, ...params);
  }

  /**
   * Get count of resources overtime
   * @Return: EntitiesResponse<CountDataPoint>
   */
  getResourcesCountOvertime(from?: number, to?: number) {
    const params = new Array();		
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }

		return this.rest.get(`${this.baseUrl}/resources-overtime`, ...params);
  }

  /**
   * Get count of resources overtime
   * @Return: EntitiesResponse<CountDataPoint>
   */
  getDayOfWeekDistribution(from?: number, to?: number) {
    const params = new Array();		
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }

		return this.rest.get(`${this.baseUrl}/count-by-dow`, ...params);
  }

}
